import isJestTest from '../data/is-jest-test';

/**
 * @function consoleInfo
 * @description Logs console info if console is available and we arent running Jest
 *
 * @param message
 */

export default function consoleInfo( message = '' ) {
	if ( process?.env?.NODE_ENV !== 'production' ) {
		if ( window.console && !isJestTest() ) {
			console.info( message );
		}
	}
}
